import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/layout'
import Entry from '../components/entry'
import Typography from '@material-ui/core/Typography'
import withRoot from '../withRoot'

const IndexPage = ({ data }) => {
  let entriesData = data.allMarkdownRemark.edges.map(entry => {
    return {
      date: entry.node.frontmatter.date,
      category: entry.node.frontmatter.category,
      course: entry.node.frontmatter.course
        ? entry.node.frontmatter.course
        : '',
      courseUrl: entry.node.frontmatter.courseUrl
        ? entry.node.frontmatter.courseUrl
        : '',
      title: entry.node.frontmatter.title,
      text: entry.node.excerpt,
    }
  })

  // let diagramData = {
  //   ue: 0,
    
  //   cs: 0,
  //    js:0,
      
  //   cpp:0
  // }
  // entriesData.forEach(data => {
  //   switch(data.category) {
  //     case 'js':
  //       diagramData.js++
  //       break;
  //     case 'ue':
  //       diagramData.ue++
  //       break;
  //     case 'cs':
  //       diagramData.cs++
  //       break;
  //     case 'cpp':
  //       diagramData.cpp++
  //       break;
  //     default:
  //       break;
  //   }
  // });

  // console.log(diagramData) 



  let entries = entriesData.map((entry, index) => (
    <Entry
      key={index}
      date={entry.date}
      title={entry.title}
      category={entry.category}
      content={
        <Typography
          style={{ marginBottom: 10 }}
          variant="body1"
          color="inherit"
        >
          {entry.text}
        </Typography>
      }
      course={entry.course}
      courseUrl={entry.courseUrl}
    />
  ))

  return (
    <Layout title="Index">
      <Typography style={{ marginBottom: 10 }} variant="h6" color="inherit">
        What i've learned so far:
      </Typography>
      {entries}
    </Layout>
  )
}

export default withRoot(IndexPage)

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          frontmatter {
            title
            date
            course
            courseUrl
            category
          }
          excerpt
        }
      }
    }
  }
`
